.header-mobile {
    padding: 10px 15px 5px;
    @include hmax(720) {
        padding: 10px 10px 0px; }
    .header-profile {
        display: flex;
        align-items: center;
        @include rmin(900) {
            display: none; }
        .profile-img {
            .img {
                width: 45px;
                height: 45px;
                object-fit: contain;
                border-radius: 100%; } }
        .page-title {
            font-size: 28px;
            line-height: 33px;
            margin-left: 20px;
            &::first-letter {
                color: $current; } } }
    .add-link {
        margin-top: 15px;
        margin-bottom: 10px;
        @include rmax(720) {
            margin: 7px 0; } } }
